import { writable, derived } from 'svelte/store'

export const segment = writable('')
export const query = writable({})
export const urlData = derived([segment, query], ([$segment, $query]) => ({
  segment: $segment,
  query: $query,
}))

export function setRouter(s, q) {
  segment.set(s)

  const qmap = new URLSearchParams(q)
  let resQ = {}
  for (let qel of qmap.entries()) {
    resQ[qel[0]] = qel[1]
  }
  query.set(resQ)
}

export const component = writable(null)
export const componentAttr = writable({})
export const componentModal = writable(null)
