// Stores
import { setRouter, urlData } from '../stores/router'

import Projects, * as ProjectsAppBar from './Projects.svelte'
import Entities, * as EntitiesAppBar from './Entities.svelte'

const routerMap = ['projects', 'entities', 'users']

// Mapeamento de rotas
export default function(router) {
  router.on(function(q) {
    setRouter('', q)
  })

  routerMap.forEach(el => {
    router.on(el, function(_, q) {
      setRouter(el, q)
    })
  })

  router
    .notFound(function() {
      setRouter('404', '')
    })
    .resolve()

  urlData.subscribe(val => {
    let q = Object.entries(val.query)
      .map(e => e.join('='))
      .join('&')

    let url = val.segment

    if (q) {
      url += '?' + q
    }

    router.pause()
    router.navigate(url)
    router.resume()
  })
}

export function getPage(segment) {
  switch (segment) {
    case 'users':
    case 'entities':
      return Entities
    case 'projects':
    default:
      return Projects
  }
}

export function getAppBarContent(segment) {
  switch (segment) {
    case 'users':
    case 'entities':
      return EntitiesAppBar
    case 'projects':
    default:
      return ProjectsAppBar
  }
}
