<script>
  import { query } from '../stores/router'

  // Helpers
  import { slugifyConfigured } from '../helpers'

  // Components
  import Input from './Input.svelte'
  import AddModal from './AddModal.svelte'
  import Button from './Button.svelte'

  // Stores
  import { listFields } from '../stores/project'

  let name = ''
  let slug = ''

  let focusName = false
  let close

  function clear() {
    name = ''
    slug = ''
  }

  let inputNode

  function callOpen({ detail }) {
    if (detail && inputNode) {
      inputNode.focus()
      inputNode.select()
    }
    if (!detail) clear()
  }

  function slugDisgest(text) {
    let s = slugifyConfigured(text)

    if (text.trim() !== s) {
      return s
    }
    return text
  }

  $: if (focusName) slug = name

  function saveField() {
    if (close) close()
    setTimeout(() => {
      listFields.add({
        name,
        slug,
      })
      $query.field = slug
      clear()
    }, 500)
  }
</script>

<style>
  div.field-form {
    width: 100%;
    display: grid;
    padding: 8px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-gap: 8px;
    justify-content: center;
    align-content: flex-start;
  }

  div.field-form .fullWidth {
    grid-column-start: 1;
    grid-column-end: 3;
    display: flex;
    flex-direction: column;
  }

  div.field-actions {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: stretch;
    padding: 16px 32px;
  }
</style>

<AddModal title="Novo Campo" on:open={callOpen} bind:close>
  <div class="field-form">
    <div class="fullWidth">
      <Input
        label="Nome da Campo"
        bind:node={inputNode}
        bind:value={name}
        on:input={() => (focusName = true)}
        on:blur={() => (focusName = false)} />
    </div>
    <div class="fullWidth">
      <Input bind:value={slug} digest={slugDisgest} label="Slug do Campo" />
    </div>
  </div>
  <div class="field-actions">
    <Button on:click={saveField}>criar campo</Button>
  </div>
</AddModal>
