<script context="module">
  import EntitiesAppBar from './_EntitiesBar.svelte'
  import EntitiesAppCard from './_EntitiesCard.svelte'

  export let bar = EntitiesAppBar
  export let component = EntitiesAppCard
</script>

<script>
  import { onMount } from 'svelte'

  // Motion
  import anime from 'animejs'
  import { fly } from 'svelte/transition'

  // Stores
  import { searchTitle } from '../stores/main'
  import { componentAttr, componentModal, query } from '../stores/router'
  import { listEntities, listFields } from '../stores/project'

  // Components
  import AddEntityModal from '../components/AddEntityModal.svelte'
  import AddFieldModal from '../components/AddFieldModal.svelte'
  import SwipeView from '../components/SwipeView.svelte'
  import DragList from '../components/DragList.svelte'
  import ListItem from '../components/ListItem.svelte'
  import IconButton from '../components/IconButton.svelte'

  let index = 0

  $: third = 'field' in $query
  $: second = 'entity' in $query && !third
  $: first = !second && !third

  $: switch (true) {
    case third:
      index = 2
      break
    case second:
      componentModal.set(AddFieldModal)
      index = 1
      break
    case first:
    default:
      componentModal.set(AddEntityModal)
      index = 0
  }

  onMount(() => {
    searchTitle.set('Buscar...')
    componentModal.set(AddEntityModal)
    componentAttr.set({})
  })
</script>

<style>
  :root {
    --entities-width: 360px;
  }

  section {
    overflow: hidden;
    width: 100%;
    min-height: 150vh;
  }

  div {
    padding: 16px 8px;
  }
</style>

<section transition:fly={{ x: 300 }}>
  <SwipeView {index}>
    <div>
      <DragList bind:items={$listEntities} let:item={entity} key="slug">
        <ListItem
          actions={['delete', 'more_vert']}
          on:click={() => ($query.entity = entity.slug)}>
           {entity.name}
        </ListItem>
      </DragList>
    </div>
    <div>
      <DragList bind:items={$listFields} let:item={field} key="slug">
        <ListItem
          actions={['delete', 'more_vert']}
          on:click={() => ($query.field = field.slug)}>
           {field.name}
        </ListItem>
      </DragList>
    </div>
    <div>C</div>
  </SwipeView>
</section>
