<script>
  import { onMount } from 'svelte'
  import { fade } from 'svelte/transition'

  // Routes
  import routesConfig, { getPage, getAppBarContent } from './routes'

  // Stores
  import { searchTitle } from './stores/main'
  import { segment, componentAttr, componentModal } from './stores/router'

  // Components
  import Input from './components/Input.svelte'
  import AddProjectModal from './components/AddProjectModal.svelte'
  import ProjectCard from './components/ProjectCard.svelte'

  export let mainRouter
  routesConfig(mainRouter)

  let fixedSection
  let windowY

  let bar
  let searchText
  let component

  $: {
    const ab = getAppBarContent($segment)
    bar = ab.bar
    component = ab.component
  }

  onMount(() => {
    setInitLoading(false)
  })
</script>

