    max-width: 360px;
    min-height: 100vh;
    margin: auto;
  }

  div {
    box-sizing: border-box;
  }

  div.header-section {
    background-color: inherit;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 248px;
  }

  div.header-section > div.bar {
    position: relative;
    /* height: 144px; */
    height: 128px;
    width: 100%;
  }

  div.header-section > div.search {
    display: flex;
    flex-direction: column;
    height: 104px;
    padding: 24px 16px;
    width: 100%;
    max-width: 344px;
  }

  div.header-section > div.bar > :global(*) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
  }

  div.header-section > div.bar > h1 {
    opacity: 0.12;
    text-align: center;
    top: unset;
    z-index: 0;
  }

  div.fixed-section {
    position: sticky;
    top: 0px;
    background-color: inherit;
    width: 100%;
    padding: 8px 16px;
    overflow: visible;
    z-index: 1000;
    display: grid;
    grid-template-columns: 72px 1fr;
    grid-column-gap: 16px;
  }

  div.fixed-section.elevation {
    box-shadow: var(--elevation1);
  }

  div.fixed-section > .modal,
  div.fixed-section > .card {
    position: relative;
    height: 100px;
  }

  div.content-section {
    display: grid;
    grid-template-columns: 360px;
    overflow-x: hidden;
  }

  div.content-section > :global(*) {
    grid-column: 1;
    grid-row: 1;
  }

  div.full-float > :global(*) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
  }
</style>

<svelte:window bind:scrollY={windowY} />

<main>
  <div class="header-section">
    <div class="bar">
      {#if typeof bar === 'string'}
        <h1 transition:fade={{ duration: 300 }} class="type-h2">
           {bar || 'Fluido'}
        </h1>
      {:else}
        <svelte:component this={bar} />
      {/if}
    </div>
    <div class="search">
      <Input label={$searchTitle || 'Buscar...'} emphasis />
    </div>
  </div>
  <div
