    class:elevation={fixedSection && fixedSection.offsetTop <= windowY}
    bind:this={fixedSection}>
    <div class="modal">
      <svelte:component this={$componentModal} />
    </div>
    <div class="card full-float">
      <svelte:component this={component} {...$componentAttr} />
    </div>
  </div>
