import { writable, derived } from 'svelte/store'
import { useLocalStorage } from '../helpers'
import { query } from './router'

function createList(label) {
  const { subscribe, set, update } = useLocalStorage(label, writable([]))

  return {
    subscribe,
    set,
    update,
    add: proj =>
      update(e => {
        e.splice(0, 0, proj)
        return e
      }),
    remove: slug => update(e => e.filter(p => slug !== p.slug)),
    clean: () => set([]),
  }
}

export const listProjects = createList('project-list')
export const listEntities = createList('entities-list')
export const listFields = createList('fields-list')

export const currentProject = derived(
  [query, listProjects],
  ([$query, $listProjects]) =>
    $listProjects.find(p => p.slug === $query.project || '') || {}
)

export const currentEntity = derived(
  [query, listEntities],
  ([$query, $listEntities]) =>
    $listEntities.find(p => p.slug === $query.entity || '') || {}
)

export const currentField = derived(
  [query, listFields],
  ([$query, $listFields]) =>
    $listFields.find(p => p.slug === $query.field || '') || {}
)

export const recentProject = derived(
  listProjects,
  $listProjects => $listProjects[0]
)
